.gpt__blog-container_article {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: var(--color-footer);
}
.gpt__blog-container_article-image {
  width: 100%;
  height: 100%;
  background: var(--color-footer);
}
.gpt__blog-container_article-image img {
  width: 100%;
  height: 100%;
}
.gpt__blog-container_article-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 1rem 1.5rem;
  height: 100%;
}
.gpt__blog-container_article-content p {
  font-size: 11.5px;
  font-weight: bold;
  font-family: var(--font-family);
  line-height: 35px;
  color: white;
}
.gpt__blog-container_article-content h3 {
  font-size: 25px;
  font-weight: 800;
  font-family: var(--font-family);
  line-height: 35px;
  color: white;
  margin-bottom: 5rem;
  cursor: pointer;
}
.gpt__blog-container_article-content p:last-child {
  cursor: pointer;
}
@media screen and(max-width:550px) {
  .gpt__blog-container_article-content h3 {
    font-size: 18px;
    line-height: 25px;
  }
}
